@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.store-banner{
    position: relative;
    margin-bottom: 5rem;
    @include media-breakpoint-down(lg){
        margin-bottom: 2rem;
        margin-top: 7rem;
    }
    .hero{
        @include media-breakpoint-down(lg){
            height: 451px;
        }
    }
    &-cont{
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        &-title{
            font-size: 5rem;
            font-weight: bold;
            color: #242c44;
            line-height: normal;
            margin-bottom: 9rem;
            @include media-breakpoint-down(lg){
                font-size: 25px;
                margin-bottom: 3rem;
            }
        }
        &-donations{
            display: flex;
            gap: 0 5rem;
            flex-flow: wrap;
            @include media-breakpoint-down(lg){
                gap: 38px;
            }
            &-post{
                display: flex;
                gap: 0 16px;
                img{
                    @include media-breakpoint-down(lg){
                        width: 60px;
                    }
                }
                &-cont{
                    display: flex;
                    align-items: flex-start;
                    flex-direction: column;
                    &-price{
                        font-size: 53px;
                        font-weight: bold;
                        color: #283663;
                        margin-bottom: 13px;
                        display: flex;
                        align-items: flex-start;
                        @include media-breakpoint-down(lg){
                            font-size: 28px;
                        }
                        small{
                            font-size: 20px;
                            font-weight: 600;
                            color: #283663;
                            margin-right: 9px;
                            margin-top: 10px;
                            @include media-breakpoint-down(lg){
                                font-size: 14px;
                            }
                        }
                    }
                    &-text{
                        font-size: 25px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                }
            }
        }
    }
}
.s-category{
    .apartTitle{
        padding-top: 4.6rem;
        margin-bottom: 50px;
        @include media-breakpoint-down(lg){
            padding-top: 1.5rem;
            margin-bottom: 20px;
        }
    }
    .productTabs{
        display: flex;
        justify-content: center;
        border-bottom: 0;
        margin-bottom: 3rem;
        @include media-breakpoint-down(lg){
            flex-wrap: nowrap;
            overflow-x: auto;
            justify-content: flex-start;
            border-bottom: 1px solid #dee2e6;
        }
        .nav-item{
            button.nav-link{
                border: solid 1px #e6e6e6;
                background-color: #fff;
                text-align: center;
                font-size: 14px;
                font-weight: 500;
                color: #242c44;
                padding: 13px 44px 12px 45px;
                border-radius: 0;
                border-left: 0;
                @include media-breakpoint-down(lg){
                    white-space: nowrap;
                }
                &.active{
                    background-color: #283663;
                    color: #fff;
                }
            }
            &:nth-child(1){
                border-left: solid 1px #e6e6e6;
            }
        }
    }
    &-celebrity{
        .apartTitle{
            padding-top: 5.6rem;
            margin-bottom: 50px;
            @include media-breakpoint-down(lg){
                padding-top: 1.5rem;
                margin-bottom: 20px;
            }
        }
        .mobile-slide{
            @include media-breakpoint-down(lg){
                flex-wrap: nowrap;
                overflow-x: auto;
            }
        }
    }
}
