@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.productWrap{
    .product-widget{
        box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.16);
        background-color: #fff;
        padding: 0;
        text-align: center;
        margin-bottom: 50px;
        position: relative;
        @include media-breakpoint-down(lg){
            margin-bottom: 10px;
        }
        &-image{
        }
        &-body{
            padding: 0;
    
            &-title{
                font-size: 18px;
                font-weight: 500;
                color: #242c44;
                line-height: normal;
                @include media-breakpoint-down(lg){
                    font-size: 16px;
                    height: 17px;
                    overflow: hidden;
                }
            }
            &-tag{
                font-size: 14px;
                font-weight: 500;
                color: #636363;
                margin-bottom: 8px;
                line-height: normal;
                @include media-breakpoint-down(lg){
                    font-size: 12px;
                }
            }
            &-price{
                font-size: 20px;
                font-weight: 700;
                color: #283663;
                display: block;
                line-height: normal;
                margin-bottom: 15px;
                @include media-breakpoint-down(lg){
                    font-size: 18px;
                }
            }
            .btn-base{
                display: none;
                transition: all 0.5s ease-out;
            }
            &-bid{
                display: flex;
                padding: 0 12px;
                align-items: center;
                justify-content: space-between;
            }
        }
        &:hover{
            margin-bottom: 0px;
            .btn-base{
                display: flex;
            }
        }
        .favouriteWish{
            width: 38px;
            height: 37px;
            background: url(../../assets/images/icons/favourite-icon.svg) no-repeat;
            position: absolute;
            right: 10px;
            top: 10px;
            background-size: 100%;
            cursor: pointer;
            @include media-breakpoint-down(lg){
                width: 26px;
            }
        }
        .bid-image-wrap{
            position: relative;
            margin-bottom: 10px;
            height: 254px;
            overflow: hidden;
            @include media-breakpoint-down(lg){
                height: 155px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &-timestamp{
                position: absolute;
                left: 50%;
                bottom: 0px;
                transform: translateX(-50%);
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 0 10px;
                &-post{
                    width: 58px;
                    height: 62px;
                    border-radius: 5px;
                    border: solid 1px #283663;
                    background-color: #fff;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    @include media-breakpoint-down(lg){
                        width: 40px;
                        height: 42px;
                    }
                    &-days{
                        font-size: 27px;
                        font-weight: 500;
                        color: #1e1e1e;
                        display: block;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                    &-text{
                        font-size: 11px;
                        font-weight: 500;
                        color: #636363;
                        line-height: 1.82;
                        display: block;
                        @include media-breakpoint-down(lg){
                            font-size: 8px;
                        }
                    }
                }
            }
        }
    }
}

.mob-pad-top-20{
    @include media-breakpoint-down(lg){
        padding-top: 15px;
    }
}
.reset-col-padding-mobile{
    .col-6{
        &:nth-child(odd){
            padding-right: 5px;
        }
        &:nth-child(even){
            padding-left: 5px;
        }
    }
}