@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.orderManagement{
    .pending{
        color: #e68b36;
    }
    .shipped{
        color: #e6c936;
    }
    .completed{
        color: #4eba19;
    }
    .order-panel{
        padding: 20px;
        border: solid 1px #e2e2e2;
        background-color: #fff;
        margin-bottom: 10px;
        position: relative;
        @include media-breakpoint-down(lg){
            padding: 10px;
        }
        &-status{
            font-size: 18px;
            font-weight: 500;
            position: absolute;
            right: 20px;
            top: 20px;
            @include media-breakpoint-down(lg){
                max-width: 175px;
                text-align: right;
                font-size: 14px;
                right: 10px;
                top: 10px;
            }
        }
        &-id{
            font-size: 18px;
            font-weight: 600;
            color: #242c44;
            margin-bottom: 12px;
            display: flex;
        }
        &-title{
            font-size: 16px;
            font-weight: 500;
            color: #283663;
            margin-bottom: 10px;
            display: flex;
        }
    }
    .op-product{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding: 10px 0;
        &:nth-child(1){
            padding-bottom: 0;
        }
        &-post{
            display: flex;
            gap: 20px;
            &-img{
                width: 80px;
                height: 80px;
                overflow: hidden;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            &-title{
                &-name{
                    font-size: 18px;
                    font-weight: 500;
                    color: #1e1e1e;
                    margin-bottom: 10px;
                }
                &-meta{
                    display: flex;
                    gap: 0 40px;
                    &-qnty{
                        font-size: 14px;
                        font-weight: 500;
                        color: #636363;
                        @include media-breakpoint-down(md){
                            font-size: 12px;
                        }
                    }
                }
            }
        }
        &-price{
            font-size: 16px;
            font-weight: 500;
            color: #1e1e1e;
            @include media-breakpoint-down(lg){
                display: none;
            }
        }
    }
    .op-papart{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-top: -10px;
        @include media-breakpoint-down(lg){
            margin-top: 10px;
        }
        &-item{
            font-size: 14px;
            font-weight: 500;
            color: #1e1e1e;
            display: flex;
            @include media-breakpoint-down(lg){
                display: none;
            }
        }
        &-price{
            &-group{
                display: flex;
                flex-direction: column;
                gap: 20px 0;
                &-list{
                    display: flex;
                    gap: 0px 25px;
                    justify-content: space-between;
                    &-ctext{
                        font-size: 16px;
                        font-weight: normal;
                        color: #212121;
                    }
                    &-promo{
                        font-size: 16px;
                        font-weight: 500;
                        color: #c00;
                    }
                    &-paidtext{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                    }
                    &-tprice{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                    }
                    &.promo-code{
                        @include media-breakpoint-down(lg){
                            display: none;
                        }
                    }
                }
            }
        }
    }
}