@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.s-category{
    .main-top-padding {
        @include media-breakpoint-down(lg){
            display: none;
        }
    }
    .infinite-scroll-component{
        overflow: unset !important;
    }
}
.showingText{
    margin-bottom: 20px;
    display: block;
    @include media-breakpoint-down(lg){
        position: absolute;
        top: 5px;
        right: 15px;
    }
    &-label{
        font-size: 18px;
        font-weight: 600;
        color: #636363;
        @include media-breakpoint-down(lg){
            display: none;
        }
    }
    &-count{
        font-size: 18px;
        color: #283663;
    }
}

.category-widget-panel{
    @include media-breakpoint-down(lg){
        display: none;
    }
}
.category-widget{
    margin-bottom: 40px;
    &-title{
        font-size: 25px;
        font-weight: 600;
        color: var(--primary-small-text);
        margin-bottom: 20px;
        @include media-breakpoint-down(lg){
            margin-bottom: 0;
            font-size: 18px;
        }
    }
    &-list{
        border-radius: 0;
        @include media-breakpoint-down(lg){
            display: none;
        }
        &-item{
            border: 0 !important;
            padding-left: 0 !important;
            a{
                font-size: 16px;
                font-weight: 400;
                color: #212121;
                &:hover{
                    color: var(primary-color);
                }
            }
            &.active{
                color: var(primary-color);
                font-weight: 600;
                background: none !important;
                :hover{
                    color: var(primary-color);
                }
            }
        }
    }
}
.range-amount{
    font-size: 18px;
    font-weight: 600;
    color: var(primary-color);
}
.border-right{
    position: relative;
    height: 100%;
    &:after{
        content: "";
        width: 1px;
        height: 100%;
        background-color: #dee2e6;
        position: absolute;
        right: 0;
        top: 0;
        @include media-breakpoint-down(lg){
            display: none;
        }
    }
}
.mobile-category{
    display: none;
    @include media-breakpoint-down(lg){
        display: flex;
        gap: 0 12px;
        align-items: center;
    }
    &-menuicon{
        display: none;
        @include media-breakpoint-down(lg){
            display: block;
            i{
                font-size: 26px;
            }
        }
    }
}