@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.productWrap{
    .product-widget{
        box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.16);
        background-color: #fff;
        padding: 0;
        text-align: center;
        margin-bottom: 50px;
        position: relative;
        @include media-breakpoint-down(lg){
            margin-bottom: 10px;
        }
    
        &-image{
        }
        &-body{
            padding: 0;
            &-title{
                font-size: 18px;
                font-weight: 500;
                color: #242c44;
                line-height: normal;
                @include media-breakpoint-down(lg){
                    font-size: 16px;
                    height: 17px;
                    overflow: hidden;
                }
            }
            &-tag{
                font-size: 14px;
                font-weight: 500;
                color: #636363;
                margin-bottom: 8px;
                line-height: normal;
                @include media-breakpoint-down(lg){
                    font-size: 12px;
                }
            }
            &-price{
                font-size: 20px;
                font-weight: 700;
                color: #283663;
                display: block;
                line-height: normal;
                margin-bottom: 15px;
                @include media-breakpoint-down(lg){
                    font-size: 18px;
                }
            }
            .btn-base{
                display: none;
                transition: all 0.5s ease-out;
            }
        }
        &:hover{
            margin-bottom: 0px;
            .btn-base{
                display: flex;
            }
        }
        .favouriteWish{
            width: 38px;
            height: 37px;
            background: url(../../assets/images/icons/favourite-icon.svg) no-repeat;
            position: absolute;
            right: 10px;
            top: 10px;
            background-size: 100%;
            cursor: pointer;
            @include media-breakpoint-down(lg){
                width: 26px;
            }
        }
        &-bid{
            display: flex;
            padding: 0 12px;
            align-items: center;
            justify-content: space-between;
        }
    }
}
