@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.celebrity-apart{
    .c-widget{
        background-color: #fff;
        padding: 0;
        text-align: center;
        margin-bottom: 20px;
        position: relative;
        border: none;
        @include media-breakpoint-down(lg){
            margin-bottom: 10px;
        }
        &-image{
            height: 450px;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &-body{
            padding: 0;
            &-title{
                font-size: 20px;
                font-weight: 600;
                color: #283663;
                line-height: normal;
                padding: 20px 15px;
                text-align: center;
                @include media-breakpoint-down(lg){
                    font-size: 16px;
                }
            }
        }
    }
}
