@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.bankAccount-Page{
    padding-bottom: 3rem;
    .row{
        .mobile-pad-reset{
            @include media-breakpoint-down(lg){
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    .pageTitle{
        text-align: center;
        @include media-breakpoint-down(md){
            text-align: left;
        }
    }
    .main-top-padding{
        margin-bottom: 5rem;
        margin-top: 4rem;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
            margin-top: 0;
        }
    }
    .border-wrap{
        padding: 19px 20px 19px 25.5px;
        border: solid 1px #e2e2e2;
        background-color: #fff;
        position: relative;
        @include media-breakpoint-down(lg){
            padding: 19px 15px 19px;
        }
        .custom-dropdown{
            position: absolute;
            right: 10px;
            top: 15px;
            .seller-status-overflowBtn{
                background: #fff;
            }
            &-menu{
                min-width: 12rem;
            }
        }
        .bankAccount-block{
            &-info{
                display: flex;
                align-items: center;
                gap: 0 25px;
                margin-bottom: 20px;
                flex-flow: wrap;
                &-title{
                    font-size: 20px;
                    font-weight: 600;
                    color: #323c5a;
                    @include media-breakpoint-down(lg){
                        display: flex;
                        order: 0;
                        font-size: 18px;
                        margin-bottom: 5px;
                    }
                }
                &-phone{
                    font-size: 20px;
                    font-weight: 600;
                    color: #323c5a;
                    @include media-breakpoint-down(lg){
                        display: flex;
                        order: 2;
                        font-size: 14px;
                    }
                }
            }
            &-text{
                font-size: 20px;
                font-weight: 500;
                color: #636363;
                max-width: 440px;
                margin-bottom: 10px;
                &-bold{
                    font-size: 20px;
                    color: #242c44;
                    font-weight: 600;
                    &.m-gap{
                        margin-left: 10px;
                    }
                }
            }
        }
        &.active{
            border: solid 1px #283663;
        }
    }
    .bankadd-btn{
        font-size: 18px;
        font-weight: 600;
        color: #283663;
        position: relative;
        cursor: pointer;
        display: flex;
        gap: 0 30px;
        align-items: center;
        justify-content: flex-start;
        &:hover{
            color: var(--primary-small-text);
        }
        &:before{
            content: '';
            width: 15px;
            height: 15px;
            background: url(../../assets/images/icons/plus-icon.svg) no-repeat;
            background-size: 100%;
        }
    }
    .mobile-align-end{
        @include media-breakpoint-down(md){
            flex-direction: row-reverse;
        }
    }
}