@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.packages{
    .card-group{
        gap: 0 30px;
    }
    .card{
        border: solid 1px var(--site-gray-border) !important;
        border-radius: 0 !important;
        @include media-breakpoint-down(lg){
            margin-bottom: 1rem !important;
        }
        &-header{
            background-color: var(--btn-base-bg);
            color: #fff;
            padding: 14px;
            text-align: center;
            font-size: 18px;
            font-weight: 600;
        }
        &-body{
            ul{
                padding: 0 20px;
            }
        }
        &-title{
            font-size: 30px;
            font-weight: 600;
            color: var(--primary-small-text);
            margin: 33px 0 25px;
            justify-content: center;
            display: flex;
        }
        &-footer{
            background: none;
            border: 0;
            padding: 8px 15px 15px;
            .btn{
                height: 44px;
            }
        }
    }
}