@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.otherProfilefeed{
    border: solid 1px #e2e2e2;
    border-radius: 5px;
    .infinite-scroll-component{
        overflow: unset !important;
    }
    @include media-breakpoint-down(sm){
        border: 0;
    }
    .ou-profile{
        @include media-breakpoint-down(sm){
            padding-left: 15px;
            padding-right: 15px;
        }
        &-cover{
            width: 100%;
            height: 174px;
            overflow: hidden;
            background-color: var(--primary-color);
            border-radius: 5px 5px 0 0;
        }
        &-cont{
            padding-left: 30px;
            padding-right: 30px;
            margin-top: -4rem;
            margin-bottom: 4rem;
            @include media-breakpoint-down(lg){
                margin-top: -6rem;
                margin-bottom: 2rem;
            }
            &-info{
                display: flex;
                flex-wrap: wrap;
                gap: 13px;
                margin-bottom: 15px;
                align-items: center;
                @include media-breakpoint-down(lg){
                    flex-direction: column;
                }
                &-image{
                    width: 170px;
                    height: 170px;
                    overflow: hidden;
                    background: url(../../assets/images/placeholder/profile-default.svg) no-repeat;
                    background-size: 100%;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                &-cont{
                    padding-top: 3rem;
                    @include media-breakpoint-down(lg){
                        text-align: center;
                        padding-top: 15px;
                    }
                    &-name{
                        font-size: 35px;
                        font-weight: 600;
                        color: var(--primary-small-text);
                        margin-bottom: 8px;
                        @include media-breakpoint-down(lg){
                            font-size: 20px;
                        }
                    }
                    &-value{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                }
            }
            &-desc{
                &-text{
                    font-size: 18px;
                    font-weight: normal;
                    line-height: 1.83;
                    color: #212121;
                    max-width: 630px;
                    @include media-breakpoint-down(lg){
                        font-size: 14px;
                        text-align: center;
                        margin: 0 auto;
                    }
                }
            }
        }
    }

    .ouProfile-tab{
        &-group{
            border-top: solid 1px #e2e2e2;
            border-bottom: 0;
            display: flex;
            justify-content: center;
            gap: 0 8px;
            .nav-link{
                border: none;
                font-size: 18px;
                font-weight: 500;
                color: #636363;
                padding-top: 14px;
                @include media-breakpoint-down(lg){
                    font-size: 12px;
                }
                &.active{
                    font-weight: 600;
                    color: #283663;
                    position: relative;
                    &:after{
                        content: '';
                        width: 44px;
                        height: 3px;
                        background-color: #283663;
                        position: absolute;
                        top: 0px;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
            }
            &-tab{
                padding-top: 35px;
                position: relative;
                padding-left: 15px;
                padding-right: 15px;
                @include media-breakpoint-down(sm){
                    padding-top: 20px;
                    padding-left: 0;
                    padding-right: 0;
                }
                .reset-col-padding{
                    .col-4{
                        padding: 0px 10px 20px;
                        @include media-breakpoint-down(lg){
                            padding: 0 2px 2px;
                        }
                    }
                }
            }
        }
    }
}
