@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.addProductpage{
    .main-top-padding{
        margin-bottom: 5rem;
        margin-top: 4rem;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
            margin-top: 0;
        }
    }
    .mb-bottom{
        margin-bottom: 45px;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
        }
    }
    .mb-bottom-frow{
        margin-bottom: 30px;
        padding-bottom: 30px;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
            padding-bottom: 25px;
        }
    }
    .mb-mobile{
        @include media-breakpoint-down(lg){
            margin-bottom: 20px;
        }
    }
    .uploadImage{
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
        }
    }
    .row{
        .col-12{
            &:nth-child(odd){
                padding-right: 25px;
                @include media-breakpoint-down(md){
                    padding-right: 15px;;
                }
            }
            &:nth-child(even){
                padding-left: 25px;
                @include media-breakpoint-down(md){
                    padding-left: 15px;;
                }
            }
        }
    }
    .pageTitle{
        @include media-breakpoint-down(lg){
            font-size: 20px;
        }
    }
    .field-group{
        .form-control{
            font-weight: 600;
        }
    }
    .text-center-desktop {
        text-align: center;
        @include media-breakpoint-down(lg){
            text-align: left;
        }
    }
}
.block-level{
    display: flex;
    align-items: center;
    flex-flow: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
    &-title{
        font-size: 20px;
        font-weight: 600;
        color: #242c44;
    }
    &-addkey{
        font-size: 18px;
        font-weight: 500;
        color: #283663;
    }
}
.block-level-field{
    display: flex;
    align-items: center;
    flex-flow: wrap;
    justify-content: space-between;
    gap: 0 20px;
    .add-product-form{
        flex: 1 1 48%;
        max-width: 48%;
        @include media-breakpoint-down(md){
            flex: 1 1 100%;
            max-width: 100%;
        }
    }
}