@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.s-product{
    &-qty{
        padding: 15px 15.5px 13px 20px;
        background-color: #f1f1f1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 35px;
        max-width: 140px;
        &-add{
            color: #636363;
            font-size: 16px;
            cursor: pointer;
        }
        &-digit{
            font-size: 18px;
            font-weight: 500;
            color: #1e1e1e;
            background: transparent;
            border: 0;
            outline: none;
            width: 100%;
            text-align: center;
        }
        &-del{
            color: #636363;
            font-size: 16px;
            cursor: pointer;
        }
    }
}