@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.cartPage{
    padding-bottom: 4rem;
    .main-top-padding{
        margin-bottom: 3.5rem;
        margin-top: 2.5rem;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
            margin-top: 0;
        }
    }
    .mob-pad-reset{
        @include media-breakpoint-down(lg){
            padding-left: 0;
            padding-right: 0;
        }
    }
    .text-center-desktop {
        text-align: center;
        @include media-breakpoint-down(lg){
            text-align: left;
        }
    }
    .pageTitle{
        @include media-breakpoint-down(lg){
            font-size: 20px;
        }
    }
    .c-list{
        &-post{
            padding: 19px 3px 20px 0;
            border: solid 1px #e2e2e2;
            background-color: #fff;
            margin-bottom: 15px;
            &-title{
                margin-bottom: 24px;
                @include media-breakpoint-down(md){
                    margin-bottom: 0;
                }
                .form-check-input{
                    position: relative;
                    &::before{
                        content: '';
                        width: 20px;
                        height: 20px;
                        border-radius: 2px;
                        border: solid 1px #979797;
                        background-color: #fff;
                        position: absolute;
                        left: -2px;
                        top: -2px;
                    }
                    &:checked + .form-check-label{
                        &::after{
                            content: '';
                            width: 20px;
                            height: 20px;
                            border-radius: 2px;
                            border: solid 1px transparent;
                            background-color: #fff;
                            position: absolute;
                            left: -25px;
                            top: 3px;
                            background: url(../../assets/images/icons/white-tick.svg) no-repeat;
                            background-size: 100%;
                        }
                    }
                    &:focus{
                        box-shadow: none;
                        outline: 0;
                    }
                }
                &-label{
                    font-size: 18px;
                    font-weight: 600;
                    color: #283663;
                    position: relative;
                    padding-left: 10px;
                    @include media-breakpoint-down(md){
                        font-size: 16px;
                    }
                }
            }
            &-row{
                border-bottom: solid 1px #e2e2e2;
                padding-top: 18px;
                padding-bottom: 18px;
            }
            &-tr{
                position: relative;
                &.thead{
                    @include media-breakpoint-down(lg){
                        display: none;
                    }
                }
                .mob-absolute{
                    @include media-breakpoint-down(lg){
                        position: absolute;
                        left: 120px;
                        top: 30px;
                    }
                    .s-product-qty{
                        @include media-breakpoint-down(lg){
                           padding: 4px 10.5px 4px 12px;
                           max-width: 100px;
                        }
                    }
                }
                &-th{
                    font-size: 14px;
                    font-weight: 500;
                    color: #242c44;
                    margin-bottom: 12px;
                    display: flex;
                }
                .op-product{
                    display: flex;
                    justify-content: space-between;
                    align-items: flex-start;
                    @include media-breakpoint-down(md){
                        margin-bottom: 15px;
                    }
                    &-post{
                        display: flex;
                        gap: 20px;
                        &-img{
                            width: 80px;
                            height: 80px;
                            overflow: hidden;
                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                            }
                        }
                        &-title{
                            &-name{
                                font-size: 18px;
                                font-weight: 500;
                                color: #242c44;
                                margin-bottom: 10px;
                                @include media-breakpoint-down(md){
                                    font-size: 14px;
                                }
                            }
                            &-meta{
                                display: flex;
                                gap: 0 40px;
                                &-qnty{
                                    font-size: 18px;
                                    font-weight: 500;
                                    color: #242c44;
                                    @include media-breakpoint-down(md){
                                        font-size: 12px;
                                    }
                                }
                            }
                        }
                    }
                    &-price{
                        font-size: 18px;
                        font-weight: 500;
                        color: #242c44;
                        @include media-breakpoint-down(md){
                            position: absolute;
                            right: 20px;
                            bottom: 0;
                            @include media-breakpoint-down(md){
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
            &-addon{
                margin-top: 15px;
                &-title{
                    font-size: 18px;
                    font-weight: 500;
                    color: #242c44;
                    margin-bottom: 10px;
                }
                &-flex{
                    display: flex;
                    gap: 0 45px;
                    margin-bottom: 7px;
                    &-label{
                        font-size: 14px;
                        font-weight: normal;
                        color: #636363;
                        flex: 1 1 70px;
                        max-width: 70px;
                    }
                    &-price{
                        font-size: 14px;
                        font-weight: 500;
                        color: #636363;
                    }
                }
            }
            &-footer{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-top: 20px;
                &-qty{
                    font-size: 14px;
                    font-weight: 500;
                    color: #1e1e1e;
                    @include media-breakpoint-down(md){
                        font-size: 12px;
                    }
                }
                &-flex{
                    display: flex;
                    align-items: center;
                    gap:  0 23px;
                    &-label{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(md){
                            font-size: 14px;
                        }
                    }
                    &-price{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(md){
                            font-size: 14px;
                        }
                    }
                }
            }
        }
        .px-20{
            padding-left: 20px;
            padding-right: 20px;
        }
    }
}