@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.single-product{
    .s-product{
        &-title{
            font-size: 30px;
            font-weight: 600;
            color: #242c44;
            margin-bottom: 10px;
            @include media-breakpoint-down(lg){
                font-size: 20px;
            }
        }
        &-category{
            font-size: 20px;
            font-weight: 500;
            color: #283663;
            margin-bottom: 25px;
            display: flex;
            @include media-breakpoint-down(lg){
                font-size: 16px;
            }
        }
        &-price{
            margin-bottom: 34px;
            display: flex;
            flex-flow: wrap;
            gap: 0 12px;
            align-items: flex-end;
            &-offer{
                font-size: 30px;
                font-weight: bold;
                color: #242c44;
                @include media-breakpoint-down(lg){
                    font-size: 25px;
                }
            }
            &-main{
                font-size: 18px;
                font-weight: 500;
                color: #636363;
                position: relative;
                line-height: 1.5;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
                &:after{
                    content: '';
                    width: 100%;
                    height: 1px;
                    background: #636363;
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
        &-text{
            font-size: 14px;
            font-weight: normal;
            line-height: 1.79;
            color: #212121;
            margin-bottom: 35px;
            @include media-breakpoint-down(lg){
                font-size: 12px;
            }
        }
        &-btn{
            .col-6{
                &:nth-child(2){
                    padding-left: 0;
                }
            }
            .btn-base{
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 0 11px;
                &:before{
                    content: '';
                    width: 20px;
                    height: 20px;
                    background: url(../../assets/images/icons/shopping--bag.svg) no-repeat;
                }
            }
        }
        &-meta{
            margin-top: 44px;
            border-top: solid 1px #d8d8d8;
            padding: 25px 0;
            @include media-breakpoint-down(lg){
                margin-top: 30px;
            }
            .btn-base{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 0 11px;
            &:before{
                content: '';
                width: 20px;
                height: 20px;
                background: url(../../assets/images/icons/shopping--bag.svg) no-repeat;
            }
        }
            &-group{
                &-item{
                    display: flex;
                    align-items: center;
                    gap: 0 10px;
                    border: none;
                    padding-left: 0;
                    &-label{
                        font-size: 14px;
                        font-weight: 500;
                        color: #636363;
                        flex: 1 1 20%;
                        max-width: 20%;
                    }
                    &-text{
                        font-size: 14px;
                        font-weight: 500;
                        color: #212121;
                    }
                }
            }
        }
    }
    .product-description{
        margin-top: 5.5rem;
        margin-bottom: 4rem;
        @include media-breakpoint-down(lg){
            margin-top: 0;
            margin-bottom: 0;
        }
        &-top{
            text-align: center;
            position: relative;
            border-bottom: 0;
            display: flex;
            justify-content: center;
            &:after{
                content: '';
                width: 100%;
                height: 1px;
                background-color: #d8d8d8;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                z-index: -1;
            }
            .nav-link{
                font-size: 20px;
                font-weight: 500;
                color: #979797;
                text-align: center;
                display: inline-block;
                background: #fff;
                border: 0;
                padding-right: 17px;
                padding-left: 17px;
                &.active{
                    color: #242c44;
                }
            }
        }
        &-box{
            padding-top: 40px;
            &-card{
                margin-bottom: 36px;
                border: 0;
                &-body{
                    padding: 0;
                    &-title{
                        font-size: 20px;
                        font-weight: 600;
                        color: #1e1e1e;
                        margin-bottom: 15px;
                    }
                    &-text{
                        font-size: 14px;
                        font-weight: 500;
                        color: #636363;
                    }
                }
            }
        }
    }
}