@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.purchased-order{
    .nav-tabs{
        position: relative;
        border-bottom: solid 1px #e2e2e2;
        @include media-breakpoint-down(lg){
            flex-flow: nowrap;
            overflow-x: auto;
        }
        .nav-item{
            .nav-link{
                border: none;
                background: none;
                font-size: 18px;
                font-weight: 500;
                color: #979797;
                padding-bottom: 14px;
                @include media-breakpoint-down(lg){
                    white-space: nowrap;
                    font-size: 14px;
                }
                &.active{
                    color: #283663;
                    position: relative;
                    &:after{
                        content: '';
                        width: 44px;
                        height: 3px;
                        background-color: #283663;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 0;
                    }
                }
            }
        }
    }
    .tab-content{
        padding-left: 20px;
        padding-right: 20px;
        @include media-breakpoint-down(lg){
            padding-left: 0px;
            padding-right: 0px;
        }
    }
    .purchasedOrder{
        .bg-pending{
            background-color: #e68b36;
        }
        .bg-shipped{
            background-color: #e6c936;
        }
        .bg-completed{
            background-color: #4eba19;
        }
        .order-panel{
            padding: 20px;
            border: solid 1px #e2e2e2;
            background-color: #fff;
            margin-bottom: 10px;
            position: relative;
            @include media-breakpoint-down(lg){
                padding: 10px;
            }
            &-status{
                font-size: 18px;
                font-weight: 500;
                position: absolute;
                right: 20px;
                top: 20px;
                color: #283663;
                @include media-breakpoint-down(lg){
                    text-align: right;
                    font-size: 14px;
                    right: 10px;
                    top: 10px;
                }

                &.success{
                    color: #4eba19;
                }
                &.failed{
                    color: #e68b36;
                }
                &.pending{
                    color: #e6c936;
                }
            }
            &-title{
                font-size: 18px;
                font-weight: 500;
                color: #283663;
                margin-bottom: 10px;
                display: flex;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
            }
        }
        .op-product{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            padding: 10px 0;
            &:nth-child(1){
                padding-bottom: 0;
            }
            &-post{
                display: flex;
                gap: 20px;
                @include media-breakpoint-down(lg){
                    gap: 10px;
                }
                &-img{
                    width: 80px;
                    height: 80px;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                &-title{
                    &-name{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        margin-bottom: 10px;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                    &-meta{
                        display: flex;
                        gap: 0 40px;
                        @include media-breakpoint-down(lg){
                            gap: 0 15px;
                        }
                        &-qnty{
                            font-size: 14px;
                            font-weight: 500;
                            color: #636363;
                            @include media-breakpoint-down(md){
                                font-size: 12px;
                            }
                        }
                    }
                    
                }
            }
            &-price{
                font-size: 16px;
                font-weight: 500;
                color: #1e1e1e;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
            }
        }
        .op-papart{
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-top: -10px;
            position: relative;
            @include media-breakpoint-down(lg){
                margin-top: 10px;
            }
            &-item{
                font-size: 14px;
                font-weight: 500;
                color: #1e1e1e;
                display: flex;
                @include media-breakpoint-down(lg){
                    font-size: 12px;
                }
            }
            &-price{
                &-group{
                    display: flex;
                    flex-direction: column;
                    gap: 20px 0;
                    &-list{
                        display: flex;
                        gap: 0px 25px;
                        justify-content: flex-end;
                        &-status{
                            font-size: 12px;
                            font-weight: 500;
                            letter-spacing: -0.12px;
                            text-align: center;
                            color: #fff;
                            padding: 3px 6px;
                            @include media-breakpoint-down(md){
                                font-size: 10px;
                                position: absolute;
                                left: 0px;
                                top: 0;
                            }
                        }
                        &-ctext{
                            font-size: 16px;
                            font-weight: normal;
                            color: #1e1e1e;
                        }
                        &-promo{
                            font-size: 16px;
                            font-weight: 500;
                            color: #1e1e1e;
                        }
                        &-paidtext{
                            font-size: 18px;
                            font-weight: 500;
                            color: #1e1e1e;
                            @include media-breakpoint-down(lg){
                                font-size: 14px;
                            }
                        }
                        &-tprice{
                            font-size: 18px;
                            font-weight: 500;
                            color: #1e1e1e;
                            @include media-breakpoint-down(lg){
                                font-size: 14px;
                            }
                        }
                        &.promo-code{
                            @include media-breakpoint-down(lg){
                                
                            }
                        }
                    }
                }
            }
        }
        .ph-footer{
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-top: solid 1px #e2e2e2;
            padding-top: 15px;
            margin-top: 15px;
            .btn-base{
                text-transform: capitalize;
                height: 32px;
                font-size: 14px;
                max-width: 160px;
                @include media-breakpoint-down(lg){
                    font-size: 12px;
                }
            }
            &-left{
                display: flex;
                flex-direction: column;
                gap: 10px 0;
                align-items: flex-start;
                &-text{
                    font-size: 14px;
                    font-weight: normal;
                    color: #242c44;
                    @include media-breakpoint-down(lg){
                        font-size: 12px;
                    }
                }
            }
            &-right{
                display: flex;
                flex-direction: column;
                gap: 10px 0;
                align-items: flex-end;
                &-text{
                    font-size: 14px;
                    font-weight: normal;
                    color: #242c44;
                    @include media-breakpoint-down(lg){
                        font-size: 12px;
                    }
                }
            }
            &.for-desktop{
                display: flex;
                @include media-breakpoint-down(lg){
                    display: none;
                }
            }
            &.for-mobile{
                display: none;
                @include media-breakpoint-down(lg){
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: 20px 0;
                    flex-flow: wrap;
                }
                .ph-footer-right{
                    flex-direction: row;
                    flex-flow: wrap;
                    gap: 0 15px;
                    flex: 1 1 100%;
                    max-width: 100%;
                    button{
                        flex: 1 1 auto;
                        max-width: 47%;
                        height: 40px;
                        font-size: 14px;
                    }
                }
            }
        }
    }
}