@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.singleProduct{
    border: solid 1px #e2e2e2;
    border-radius: 5px;
    @include media-breakpoint-down(lg){
        border: 0;
        border-radius: 0;
    }
    &-selecttitle{
        font-size: 38px;
        font-weight: bold;
        color: #242c44;
        margin: 40px 0;
        display: block;
        text-align: center;
        @include media-breakpoint-down(lg){
            font-size: 22px;
            margin: 30px 0;
        }
    }
    .reset-col-padding{
        .col-4{
            padding: 0px 10px 20px;
            @include media-breakpoint-down(lg){
                padding: 0 2px 2px;
            }
        }
    }
}
