@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.header{
  padding: 0 20px;
  box-shadow: 0 3px 32px 0 rgba(0, 0, 0, 0.06);
  background-color: #fff;
  position: fixed;
  width: 100%;
  z-index: 9;
  @media screen and (min-width: 1600px) {
    padding: 0 90px;
  }
  @include media-breakpoint-down(lg){
    box-shadow: none;
    padding: 15px 0;
  }
  &-left{
    @include media-breakpoint-down(lg){
      padding: 0px 15px 15px;
      display: flex;
    }
  }
  &-middle{
    @include media-breakpoint-down(lg){
      order: 3;
      max-width: 100%;
      flex: 1 1 100%;
      border-top: solid 1px #e2e2e2;
      padding: 2px 15px 0;
    }
    &-nav{
      justify-content: center;
      display: flex;
      @include media-breakpoint-down(lg){
        padding: 0px;
        display: block;
      }
      &-main{
        display: flex;
        align-items: center;
        @include media-breakpoint-down(lg){
          justify-content: space-between;
        }
        &-item{
          list-style: none;
          &-link{
            display: flex;
            position: relative;
            width: 103px;
            height: 80px;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            @include media-breakpoint-down(xl){
              width: 90px;
            }
            @include media-breakpoint-down(lg){
              width: 70px;
              height: 40px;
            }
            img{
              width: 30px;
              height: 30px;
            }
          }
          &:hover, &.active{
            span{
              &:after{
                content: "";
                width: 100%;
                height: 3px;
                background-color: var(--btn-base-bg);;
                position: absolute;
                bottom: 0;
                left: 0;
              }
              img{
                filter: invert(100%) sepia(3%) saturate(5932%) hue-rotate(202deg) brightness(93%) contrast(87%);
              }
            }
          }
        }
      }
    }
  }
  &-right{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 0 40px;
      @include media-breakpoint-down(lg){
        order: 2;
        padding: 0px 15px 15px 0;
        gap: 0 15px;
      }
    .cart-options{
      &-main{
        display: flex;
        align-items: center;
        gap: 0 25px;
        @include media-breakpoint-down(lg){
          gap: 0 15px;
        }
        &-list{
          &-item{
            position: relative;
            display: flex;
              width: 30px;
              height: 30px;
              cursor: pointer;
            @include media-breakpoint-down(lg){
              width: 26px;
              height: 26px;
            }
            .notification-red{
              width: 16px;
              height: 16px;
              background-color: var(--notification-error);
              font-size: 10px;
              font-weight: 500;
              color: #fff;
              display: flex;
              justify-content: center;
              align-items: center;
              position: absolute;
              right: -4px;
              top: -4px;
              border-radius: 50%;
            }
          }
        }
      }
    }
  }
}
.header-center{
  padding: 1.594rem 0;
  text-align: center;
}
.logo {
  max-width: 110px;
  margin: 0;
  @include media-breakpoint-down(lg){
    max-width: 70px;
  }
}

