@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.productImage{
    .productImage-widget{
        border: 0;
        &-wrap{
            border: solid 1px #e2e2e2;
            max-height: 290px;
            object-fit: cover;
            position: relative;
            height: 100%;
            &-image{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &-tag{
                padding: 5px 14px 6px;
                border-radius: 5px;
                background-color: #e21e1e;
                font-size: 14px;
                font-weight: 500;
                color: #fff;
                position: absolute;
                bottom: 10px;
                right: 10px;
                white-space: nowrap;
                @include media-breakpoint-down(lg){
                    right: auto;
                    left: 50%;
                    transform: translateX(-50%);
                    font-size: 12px;
                    padding: 5px;
                }
            }
            .custom-dropdown{
                position: absolute;
                right: 12px;
                top: 12px;
                @include media-breakpoint-down(lg){
                    display: none;
                }
            }
        }
    }
}