@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.addressPage{
    padding-bottom: 3rem;
    .row{
        .mobile-pad-reset{
            @include media-breakpoint-down(lg){
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    .pageTitle{
        text-align: center;
        @include media-breakpoint-down(md){
            text-align: left;
        }
    }
    .main-top-padding{
        margin-bottom: 5rem;
        margin-top: 4rem;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
            margin-top: 0;
        }
    }
    .border-wrap{
        padding: 19px 20px 19px 25.5px;
        border: solid 1px #e2e2e2;
        background-color: #fff;
        position: relative;
        @include media-breakpoint-down(lg){
            padding: 19px 15px 19px;
        }
        .custom-dropdown{
            position: absolute;
            right: 10px;
            top: 15px;
            .seller-status-overflowBtn{
                background: #fff;
            }
            &-menu{
                min-width: 12rem;
            }
        }
        .address-block{
            min-height: 175px;
            &-info{
                display: flex;
                align-items: center;
                gap: 0 25px;
                margin-bottom: 20px;
                flex-flow: wrap;
                &-title{
                    font-size: 20px;
                    font-weight: 600;
                    color: #323c5a;
                    @include media-breakpoint-down(lg){
                        display: flex;
                        order: 0;
                        font-size: 18px;
                        margin-bottom: 5px;
                    }
                }
                &-phone{
                    font-size: 20px;
                    font-weight: 600;
                    color: #323c5a;
                    @include media-breakpoint-down(lg){
                        display: flex;
                        order: 2;
                        font-size: 14px;
                    }
                }
                &-primary{
                    padding: 7px 11px 7px 12px;
                    border-radius: 15px;
                    background-color: #283663;
                    font-size: 14px;
                    font-weight: 500;
                    color: #fff;
                    margin-left: 3rem;
                    @include media-breakpoint-down(lg){
                        display: flex;
                        order: 1;
                        font-size: 12px;
                        padding: 8px 12px;
                        margin-left: 1rem;
                    }
                }
            }
            &-text{
                font-size: 18px;
                font-weight: 500;
                color: #636363;
                max-width: 440px;
            }
        }
        &.active{
            border: solid 1px #283663;
        }
    }
    .address-btn{
        font-size: 18px;
        font-weight: 600;
        color: #283663;
        position: relative;
        cursor: pointer;
        display: flex;
        gap: 0 30px;
        align-items: center;
        justify-content: flex-start;
        &:hover{
            color: var(--primary-small-text);
        }
        &:before{
            content: '';
            width: 15px;
            height: 15px;
            background: url(../../assets/images/icons/plus-icon.svg) no-repeat;
            background-size: 100%;
        }
    }
    .mobile-align-end{
        @include media-breakpoint-down(md){
            flex-direction: row-reverse;
        }
    }
    .newAddress-form{
        padding: 20px 20px 33px;
        border: solid 1px #e2e2e2;
        background-color: #fcfcfc;
        @include media-breakpoint-down(md){
            padding: 15px;
        }
        &-title{
            font-size: 18px;
            font-weight: 600;
            color: #283663;
            margin-bottom: 30px;
        }
        &-row{
            display: flex;
            flex-flow: wrap;
            gap: 10px;
            margin-bottom: 25px;
            &-col{
                flex: 1 1 49.5%;
                max-width: 49.5%;
                @include media-breakpoint-down(lg){
                    flex: 1 1 49%;
                    max-width: 49%;
                }
                @include media-breakpoint-down(md){
                    flex: 1 1 100%;
                    max-width: 100%;
                }
                .form-control{
                    font-weight: 600;
                }
            }
        }
    }
}