@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.brand-apart{
    .brand-widget{
        box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.16);
        background-color: #fff;
        padding: 0;
        text-align: center;
        margin-bottom: 20px;
        position: relative;
        @include media-breakpoint-down(lg){
            margin-bottom: 10px;
        }
        &-image{
            height: 245px;
            background-color: #f8f8f8;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
        }
        &-body{
            padding: 0;
            &-title{
                font-size: 18px;
                font-weight: 600;
                color: #242c44;
                line-height: normal;
                padding: 20px 15px;
                text-align: center;
                @include media-breakpoint-down(lg){
                    font-size: 16px;
                }
            }
        }
    }
}
