@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.otherProfilefeed{
    border: solid 1px #e2e2e2;
    border-radius: 5px;
    @include media-breakpoint-down(sm){
        border: 0;
    }
    .ou-profile{
        @include media-breakpoint-down(sm){
            padding-left: 15px;
            padding-right: 15px;
        }
        &-cover{
            width: 100%;
            height: 174px;
            overflow: hidden;
            background-color: var(--primary-color);
            border-radius: 5px 5px 0 0;
            position: relative;
            z-index: -1;
            .edit-cover{
                padding: 10px 10px 9px 12px;
                border-radius: 5px;
                background-color: #fff;
                position: absolute;
                bottom: 20px;
                right: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 0 5px;
                @include media-breakpoint-down(lg){
                    width: 40px;
                    height: 40px;
                    gap: 0;
                    padding: 0;
                    border-radius: 50%;
                }
                &:before{
                    content: '';
                    width: 18px;
                    height: 18px;
                    background: url(../../assets/images/icons/camera.svg) no-repeat;
                    background-size: 100%;
                    @include media-breakpoint-down(lg){
                        width: 22px;
                        height: 22px;
                    }
                }
                &-text{
                    font-size: 14px;
                    font-weight: 500;
                    color: #242c44;
                    @include media-breakpoint-down(lg){
                        font-size: 0;
                        color: transparent;
                    }
                }
            }
        }
        &-cont{
            padding-left: 30px;
            padding-right: 30px;
            margin-top: -4rem;
            margin-bottom: 4rem;
            @include media-breakpoint-down(lg){
                margin-top: -6rem;
                margin-bottom: 2rem;
            }
            &-info{
                display: flex;
                flex-wrap: wrap;
                gap: 13px;
                margin-bottom: 15px;
                align-items: center;
                @include media-breakpoint-down(lg){
                    flex-direction: column;
                }
                &-image{
                    width: 170px;
                    height: 170px;
                    overflow: hidden;
                    background: url(../../assets/images/placeholder/profile-default.svg) no-repeat;
                    background-size: 100%;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                &-cont{
                    padding-top: 3rem;
                    @include media-breakpoint-down(lg){
                        text-align: center;
                        padding-top: 15px;
                    }
                    &-name{
                        font-size: 35px;
                        font-weight: 600;
                        color: var(--primary-small-text);
                        margin-bottom: 8px;
                        @include media-breakpoint-down(lg){
                            font-size: 20px;
                        }
                    }
                    &-value{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                }
            }
            &-withtext{
                display: flex;
                @include media-breakpoint-down(lg){
                    flex-direction: column;
                }
                .seller-status{
                    display: flex;
                    justify-content: center;
                    margin-left: auto;
                    align-items: center;
                    gap: 0 20px;
                    margin-top: -11rem;
                    white-space: nowrap;
                    cursor: pointer;
                    @include media-breakpoint-down(lg){
                        margin-top: 20px;
                    }
                    &-active {
                        text-transform: capitalize;
                        font-size: 14px;
                        padding: 10px 15px 9px;
                        height: auto;
                        border-radius: 5px;
                        font-weight: 500;
                    }
                    &-overflowBtn{
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 2px;
                        background-color: #eee;
                        border-radius: 5px;
                        cursor: pointer;
                        &:after{
                            content: "";
                            width: 30px;
                            height: 30px;
                            background: url(../../assets/images/icons/overflow-menu--horizontal.svg) no-repeat;
                        }
                    }
                }
                .approval-status{
                    display: flex;
                    justify-content: center;
                    margin-left: auto;
                    align-items: center;
                    gap: 0 20px;
                    margin-top: 5rem;
                    white-space: nowrap;
                    cursor: pointer;
                    &-active {
                        text-transform: capitalize;
                        font-size: 14px;
                        padding: 10px 15px 9px;
                        height: auto;
                        background: #e68b36;
                        border-radius: 5px;
                        font-weight: 500;
                    }
                    &-overflowBtn{
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 2px;
                        background-color: #eee;
                        border-radius: 5px;
                        cursor: pointer;
                        &:after{
                            content: "";
                            width: 30px;
                            height: 30px;
                            background: url(../../assets/images/icons/overflow-menu--horizontal.svg) no-repeat;
                        }
                    }
                }
            }
            &-desc{
                &-text{
                    font-size: 18px;
                    font-weight: normal;
                    line-height: 1.83;
                    color: #212121;
                    max-width: 630px;
                    @include media-breakpoint-down(lg){
                        font-size: 14px;
                        text-align: center;
                        margin: 0 auto;
                    }
                }
            }
        }
    }

    .myProfile-tab{
        position: relative;
        &-group{
            border-top: solid 1px #e2e2e2;
            border-bottom: 0;
            display: flex;
            justify-content: center;
            gap: 0 80px;
            @include media-breakpoint-down(lg){
                gap: 0 42px;
            }
            .nav-item:nth-child(1){
                .nav-link{
                    position: relative;
                    color: transparent;
                    font-size: 0;
                    padding-top: 38px;
                    border: none;
                    &:before{
                        content: '';
                        width: 30px;
                        height: 30px;
                        background: url(../../assets/images/icons/newsfeed.svg) no-repeat;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 0;
                    }
                }
            }
            .nav-item:nth-child(2){
                .nav-link{
                    position: relative;
                    color: transparent;
                    font-size: 0;
                    padding-top: 38px;
                    border: none;
                    &:before{
                        content: '';
                        width: 30px;
                        height: 30px;
                        background: url(../../assets/images/icons/product.svg) no-repeat;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 0;
                    }
                }
            }
            .nav-item:nth-child(3){
                .nav-link{
                    position: relative;
                    color: transparent;
                    font-size: 0;
                    padding-top: 38px;
                    border: none;
                    &:before{
                        content: '';
                        width: 21px;
                        height: 30px;
                        background: url(../../assets/images/icons/order.svg) no-repeat;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 0;
                    }
                }
            }
            .nav-item:nth-child(4){
                .nav-link{
                    position: relative;
                    color: transparent;
                    font-size: 0;
                    padding-top: 38px;
                    border: none;
                    &:before{
                        content: '';
                        width: 30px;
                        height: 30px;
                        background: url(../../assets/images/icons/purchase-history.svg) no-repeat;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 0;
                    }
                }
            }
            .nav-item:nth-child(5){
                .nav-link{
                    position: relative;
                    color: transparent;
                    font-size: 0;
                    padding-top: 38px;
                    border: none;
                    &:before{
                        content: '';
                        width: 30px;
                        height: 30px;
                        background: url(../../assets/images/icons/wallet.svg) no-repeat;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 0;
                    }
                }
            }
            .nav-link{
                &.active{
                    font-weight: 600;
                    color: #283663;
                    position: relative;
                    &:after{
                        content: '';
                        width: 44px;
                        height: 3px;
                        background-color: #283663;
                        position: absolute;
                        top: 0px;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
            }
            &-tab{
                padding-top: 6rem;
                position: relative;
                padding-left: 15px;
                padding-right: 15px;
                @include media-breakpoint-down(sm){
                    padding-top: 20px;
                    padding-left: 0;
                    padding-right: 0;
                }
                .mx-auto{
                    position: relative;
                }
                .reset-col-padding{
                    .col-4{
                        padding: 0px 10px 20px;
                        position: relative;
                        @include media-breakpoint-down(lg){
                            padding: 0 2px 2px;
                        }
                    }
                }
                .c-newPost{
                    max-width: 202px;
                    height: 37px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 0 13px;
                    position: absolute;
                    right: 15px;
                    top: -3.5rem;
                    border-radius: 5px;
                    cursor: pointer;
                    @include media-breakpoint-down(lg){
                        position: relative;
                        right: 15px;
                        top: 0;
                        margin-left: auto;
                        margin-top: 20px;
                        margin-bottom: 10px;
                        max-width: 110px;
                    }
                    &.for-desktop{
                        display: flex;
                        @include media-breakpoint-down(lg){
                            display: none;
                        }
                    }
                    &.for-mobile{
                        display: none;
                        @include media-breakpoint-down(lg){
                            display: flex;
                        }
                    }
                    &-text{
                        text-transform: capitalize;
                    }
                    &::before{
                        content: '';
                        width: 22px;
                        height: 22px;
                        background: url(../../assets/images//icons/image--copy.svg) no-repeat;
                    }
                }
                .no-products{
                    padding-top: 4.5rem;
                    position: relative;
                    @include media-breakpoint-down(lg){
                        margin-top: 2rem;
                    }
                    &:before{
                        content: '';
                        width: 50px;
                        height: 50px;
                        background: url(../../assets/images/icons/no-product.svg) no-repeat;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        top: 0;
                    }
                    &-title{
                        font-size: 18px;
                        font-weight: 600;
                        color: #1e1e1e;
                        margin-bottom: 10px;
                    }
                    &-text{
                        font-size: 14px;
                        font-weight: normal;
                        color: #1e1e1e;
                        margin-bottom: 25px;
                    }
                    &-btn{
                        text-transform: capitalize;
                        height: 37px;
                        border-radius: 5px;
                        cursor: pointer;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}
