@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.v-box{
    box-shadow: 0 3px 17px 0 rgba(0, 0, 0, 0.05);
    background-color: #fff;
    padding: 35.4px 45px 43px 49px;
    @include media-breakpoint-down(lg){
        box-shadow: none;
        padding: 0;
    }
    &-icon{
        margin-bottom: 30px;
    }
    &-title{
        text-align: center;
        margin-bottom: 30px;
        h2{
            font-size: 30px;
            font-weight: 700;
            color: var(--primary-small-text);
            @include media-breakpoint-down(lg){
                font-size: 20px;
                font-weight: 600;
            }
        }
    }
    &-text{
        margin-bottom: 35px;
        text-align: center;
        p{
            font-size: 18px;
            font-weight: 500;
            color: #636363;
            span{
                display: flex;
                justify-content: center;
                color: #283663;
            }
        }
    }
    &-otp{
        margin-bottom: 40px;
        form{
            .form-wrap{
                display: flex;
                flex-flow: wrap;
                gap: 0 20px;
                justify-content: center;
            }
        }
        .otp{
            width: 40px;
            height: 40px;
            border: 0;
            border-bottom: solid 2px  #a8a8a8;
            font-size: 36px;
            font-weight: 500;
            color: var(--primary-small-text);
            text-align: center;
            padding: 2px 0 12px;
            border-radius: 0;
            background: none;
            &:focus, &:active{
                outline: none;
                border: 0;
                box-shadow: none !important;
                border-bottom: solid 2px var(--primary-color);
                background: none;
            }
            &:disabled, &.no-validate{
                outline: none;
                border: 0;
                box-shadow: none !important;
                border-bottom: solid 2px var(--red-text-color);
                background: none;
            }
        }
        .errorText{
            display: block;
            text-align: center;
            margin-top: 20px;
        }
    }
    &-time{
        margin-top: 40px;
        text-align: center;
        margin-bottom: 60px;
        flex: 1 1 100%;
        span{
            font-size: 25px;
            font-weight: 400;
            color: var(--primary-small-text);
        }
    }
    &-btnArea{
        justify-content: flex-end;
        align-items: center;
        .align-right{
            text-align: right;
            @include media-breakpoint-down(lg){
                text-align: center;
                margin-bottom: 1rem;
            }
        }
    }
}