@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.productSwiper-wrap{
    display: flex;
    gap: 0 20px;
    padding-right: 3rem;
    @include media-breakpoint-down(lg){
        padding-right: 0;
      }
    &-right{
        position: relative;
        flex: 1 1 80%;
        max-width: 80%;
        @include media-breakpoint-down(lg){
            flex: 1 1 100%;
            max-width: 100%;
        }
    }
    .swiper-slide2{
        border: solid 1px #e8e8e8;
        @include media-breakpoint-down(lg){
            border: 0;
            margin-bottom: 20px;
        }
    }
    .swiper-button-next, .swiper-button-prev{
        color: var(--base-theme-blue);
        &:after{
            @include media-breakpoint-down(lg){
                font-size: 30px;
            }
        }
    }
    .swiper-slide{
        text-align: center;
    }
    .swiper-thumb{
        display: flex;
        flex: 1 1 20%;
        max-width: 20%;
        max-height: 500px;
        @include media-breakpoint-down(lg){
            display: none;
          }
        &-slide{
            height: auto;
            width: 100% !important;
            border: solid 1px #e8e8e8;
            margin-bottom: 20px;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &.swiper-slide-active{
                border: solid 1px #283663;
            }
        }
        .swiper-wrapper{
            display: flex;
            flex-direction: column;
        }
    }
}