@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.single-product{
    .productSwiper-wrap{
        display: flex;
        gap: 0 20px;
        padding-right: 3rem;
        @include media-breakpoint-down(lg){
            padding-right: 0;
          }
        &-right{
            position: relative;
            flex: 1 1 80%;
            max-width: 80%;
            @include media-breakpoint-down(lg){
                flex: 1 1 100%;
                max-width: 100%;
            }
        }
        .swiper-slide2{
            border: solid 1px #e8e8e8;
            @include media-breakpoint-down(lg){
                border: 0;
                margin-bottom: 20px;
            }
        }
        .swiper-button-next, .swiper-button-prev{
            color: var(--base-theme-blue);
            &:after{
                @include media-breakpoint-down(lg){
                    font-size: 30px;
                }
            }
        }
        .swiper-slide{
            text-align: center;
        }
        .swiper-thumb{
            display: flex;
            flex: 1 1 20%;
            max-width: 20%;
            max-height: 500px;
            @include media-breakpoint-down(lg){
                display: none;
              }
            &-slide{
                height: auto;
                width: 100% !important;
                border: solid 1px #e8e8e8;
                margin-bottom: 20px;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                &.swiper-slide-active{
                    border: solid 1px #283663;
                }
            }
            .swiper-wrapper{
                display: flex;
                flex-direction: column;
            }
        }
        .bid-image-wrap{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 0px;
            width: 100%;
            z-index: 1;
            @include media-breakpoint-down(lg){
                bottom: 2rem;
              }
            &-timestamp{
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 0 10px;
                &-post{
                    width: 58px;
                    height: 62px;
                    border-radius: 5px;
                    border: solid 1px #283663;
                    background-color: #fff;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    &-days{
                        font-size: 27px;
                        font-weight: 500;
                        color: #1e1e1e;
                        display: block;
                    }
                    &-text{
                        font-size: 11px;
                        font-weight: 500;
                        color: #636363;
                        line-height: 1.82;
                        display: block;
                    }
                }
            }
        }
    }
    .s-product{
        &-title{
            font-size: 30px;
            font-weight: 600;
            color: #242c44;
            margin-bottom: 10px;
            @include media-breakpoint-down(lg){
                font-size: 20px;
            }
        }
        &-category{
            font-size: 20px;
            font-weight: 500;
            color: #283663;
            margin-bottom: 25px;
            display: flex;
            @include media-breakpoint-down(lg){
                font-size: 16px;
            }
        }
        &-price{
            margin-bottom: 34px;
            display: flex;
            flex-flow: wrap;
            gap: 0 12px;
            align-items: flex-end;
            &-offer{
                font-size: 30px;
                font-weight: bold;
                color: #242c44;
                @include media-breakpoint-down(lg){
                    font-size: 25px;
                }
            }
            &-main{
                font-size: 18px;
                font-weight: 500;
                color: #636363;
                position: relative;
                line-height: 1.5;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
                &:after{
                    content: '';
                    width: 100%;
                    height: 1px;
                    background: #636363;
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
            &-noamount{
                font-size: 18px;
                font-weight: 500;
                color: #636363;
                position: relative;
                line-height: 1.5;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
            }
        }
        &-text{
            font-size: 14px;
            font-weight: normal;
            line-height: 1.79;
            color: #212121;
            margin-bottom: 35px;
            @include media-breakpoint-down(lg){
                font-size: 12px;
            }
        }
        .bidDetailswrap{
            padding: 12px;
            border: solid 1px #e2e2e2;
        }
        &-bidDetails{
            align-items: center;
            @include media-breakpoint-down(lg){
                align-items: flex-start;
            }
            .currentBid{
                display: flex;
                flex-direction: column;
                gap: 4px 0;
                @include media-breakpoint-down(lg){
                    margin-bottom: 15px;
                }
                &-text{
                    font-size: 14px;
                    font-weight: normal;
                    color: #212121;
                }
                &-price{
                    font-size: 25px;
                    font-weight: 600;
                    color: #242c44;
                }
            }
            .trackBidtext{
                font-size: 14px;
                font-weight: 600;
                color: #283663;
                @include media-breakpoint-down(lg){
                    display: block;
                    text-align: right;
                    padding-top: 2px;
                }
            }
            .btn-base{
                &.noIcon{
                    &:before{
                        display: none;
                    }
                }
            }
        }
        &-meta{
            margin-top: 44px;
            border-top: solid 1px #d8d8d8;
            padding: 25px 0;
            @include media-breakpoint-down(lg){
                margin-top: 30px;
            }
            .btn-base{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 0 11px;
            &:before{
                content: '';
                width: 20px;
                height: 20px;
                background: url(../../assets/images/icons/shopping--bag.svg) no-repeat;
            }
        }
            &-group{
                &-item{
                    display: flex;
                    align-items: center;
                    gap: 0 10px;
                    border: none;
                    padding-left: 0;
                    &-label{
                        font-size: 14px;
                        font-weight: 500;
                        color: #636363;
                        flex: 1 1 20%;
                        max-width: 20%;
                    }
                    &-text{
                        font-size: 14px;
                        font-weight: 500;
                        color: #212121;
                    }
                }
            }
        }
    }
    .product-description{
        margin-top: 5.5rem;
        margin-bottom: 4rem;
        @include media-breakpoint-down(lg){
            margin-top: 0;
            margin-bottom: 0;
        }
        &-top{
            text-align: center;
            position: relative;
            border-bottom: 0;
            display: flex;
            justify-content: center;
            &:after{
                content: '';
                width: 100%;
                height: 1px;
                background-color: #d8d8d8;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                z-index: -1;
            }
            .nav-link{
                font-size: 20px;
                font-weight: 500;
                color: #979797;
                text-align: center;
                display: inline-block;
                background: #fff;
                border: 0;
                padding-right: 17px;
                padding-left: 17px;
                &.active{
                    color: #242c44;
                }
            }
        }
        &-box{
            padding-top: 40px;
            &-card{
                margin-bottom: 36px;
                border: 0;
                &-body{
                    padding: 0;
                    &-title{
                        font-size: 20px;
                        font-weight: 600;
                        color: #1e1e1e;
                        margin-bottom: 15px;
                    }
                    &-text{
                        font-size: 14px;
                        font-weight: 500;
                        color: #636363;
                    }
                }
            }
        }
        &-bidtrack{
            border: solid 1px #d4d4d4;
            margin-top: 50px;
            table{
                thead{
                    tr{
                        th{
                            font-size: 14px;
                            font-weight: 500;
                            color: #636363;
                            border-bottom: solid 1px #e2e2e2;
                            padding-top: 21px;
                            padding-bottom: 11px;
                            text-transform: uppercase;
                            @include media-breakpoint-down(lg){
                                font-size: 12px;
                                padding-left: 5px;
                                padding-right: 5px;
                            }
                            &:nth-child(1){
                                padding-left: 25px;
                                @include media-breakpoint-down(lg){
                                    padding-left: 5px;
                                }
                            }
                        }
                    }
                }
                tbody{
                    border: 0;
                    tr{
                        td{
                            font-size: 20px;
                            font-weight: 500; 
                            color: #242c44;
                            border-bottom: solid 1px #e2e2e2;
                            padding-top: 23px;
                            padding-bottom: 23px;
                            @include media-breakpoint-down(lg){
                                font-size: 12px;
                                white-space: nowrap;
                                padding-left: 5px;
                                padding-right: 5px;
                            }
                            &:nth-child(1){
                                padding-left: 25px;
                                @include media-breakpoint-down(lg){
                                    padding-left: 10px;
                                }
                            }
                        }
                        &:last-child{
                            td{
                                border-bottom: 0;
                            }
                        }
                    }
                }
            }
            &-price{
                font-size: 25px;
                font-weight: 600;
                @include media-breakpoint-down(lg){
                    font-size: 12px;
                }
            }
        }
    }
}