@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.s-notfound{
    text-align: center;
    &-item{
        font-size: 18px;
        font-weight: 500;
        color: #636363;
        margin-bottom: 44px;
        @include media-breakpoint-down(lg){
            font-size: 14px;
        }
    }
    &-image{
        margin-bottom: 34px;
        text-align: center;
    }
    &-title{
        font-size: 25px;
        font-weight: 600;
        color: #242c44;
        margin-bottom: 12px;
        @include media-breakpoint-down(lg){
            font-size: 18px;
        }
    }
    &-text{
        font-size: 14px;
        font-weight: normal;
        color: #636363;
        @include media-breakpoint-down(lg){
            font-size: 12px;
        }
    }
}