@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';

.category-carousel-wrap{
    .category-carousel{
        position: relative;
        .category-image{
            overflow: hidden;
            position: relative;
        }
        .category-title{
            padding: 13px 34px 13px 35px;
            background-color: #fff;
            text-align: center;
            font-size: 14px;
            font-weight: 600;
            color: #242c44;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
        }
    }
    .swiper-button-prev{
        &:after{
            content: "";
            width: 12px;
            height: 25px;
            background: url(../../assets/images/icons/prev-icon.svg) no-repeat;
        }
    }
    .swiper-button-next{
        &:after{
            content: "";
            width: 15px;
            height: 25px;
            background: url(../../assets/images/icons/next-icon.svg) no-repeat;
        }
    }
}
@media screen and (min-width: 576px) {
    .swiper {
      width: 100%;
    }
  }
