@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';

.mob-pad-reset{
    @include media-breakpoint-down(lg){
        padding-left: 0;
        padding-right: 0;
    }
}
.checkoutPage{
    padding-bottom: 4rem;
    .main-top-padding{
        margin-bottom: 3.5rem;
        margin-top: 2.5rem;
        @include media-breakpoint-down(lg){
            margin-bottom: 25px;
            margin-top: 0;
        }
    }
    .text-center-desktop {
        text-align: center;
        @include media-breakpoint-down(lg){
            text-align: left;
        }
    }
    .pageTitle{
        @include media-breakpoint-down(lg){
            font-size: 20px;
        }
    }
    .c-cont{
        &-adres{
            padding: 24px 25px 38px;
            border: solid 1px #e2e2e2;
            background-color: #fff;
            position: relative;
            margin-bottom: 30px;
            @include media-breakpoint-down(lg){
                padding: 15px;
                margin-left: -15px;
                margin-right: -15px;
            }
            &-titile{
                font-size: 20px;
                font-weight: 600;
                color: #323c5a;
                padding-bottom: 30px;
                @include media-breakpoint-down(lg){
                    display: none;
                }
            }
            &-edit{
                font-size: 20px;
                font-weight: 600;
                color: #283663;
                position: absolute;
                top: 24px;
                right: 25px;
                cursor: pointer;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                    top: 18px;
                }
            }
            &-flex{
                display: flex;
                align-items: center;
                padding-bottom: 20px;
                gap: 0 28px;
                @include media-breakpoint-down(lg){
                    flex-flow: wrap;
                    gap: 0 10px;
                }
                &-label{
                    font-size: 20px;
                    font-weight: 600;
                    color: #323c5a;
                    @include media-breakpoint-down(lg){
                        order: 0;
                        font-size: 18px;
                        font-weight: 500;
                    }
                }
                &-num{
                    font-size: 20px;
                    font-weight: 600;
                    color: #323c5a;
                    @include media-breakpoint-down(lg){
                        order: 2;
                        font-size: 14px;
                        font-weight: 500;
                        margin-top: 5px;
                    }
                }
                &-tag{
                    padding: 6px 11px 6px 12px;
                    border-radius: 15px;
                    background-color: #283663;
                    height: 30px;
                    font-size: 14px;
                    font-weight: 500;
                    color: #fff;
                    margin-left: 40px;
                    line-height: normal;
                    @include media-breakpoint-down(lg){
                        order: 1;
                        font-size: 12px;
                        padding: 3px 10px;
                        margin-left: 0;
                        height: 22px;
                    }
                }
            }
            &-text{
                font-size: 18px;
                font-weight: 500;
                color: #636363;
                padding-bottom: 10px;
                max-width: 440px;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
            }
        }
        &-detail{
            &-title{
                font-size: 20px;
                font-weight: 600;
                color: #323c5a;
                padding-bottom: 20px;
                @include media-breakpoint-down(lg){
                    font-size: 18px;
                }
            }
            &-flex{
                display: flex;
                align-items: flex-start;
                gap: 0 27px;
                margin-bottom: 20px;
                &-img{
                    width: 80px;
                    height: 80px;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                &-cont{
                    &-title{
                        font-size: 18px;
                        font-weight: 500;
                        color: #242c44;
                        margin-bottom: 10px;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                    &-qty{
                        font-size: 14px;
                        font-weight: 500;
                        color: #242c44;
                        @include media-breakpoint-down(lg){
                            display: none;
                        }
                    }
                }
            }
            &-opt{
                margin-bottom: 20px;
                &.mob-flex{
                    @include media-breakpoint-down(lg){
                        display: flex;
                        gap:  0 10px;
                    }
                }
                &-title{
                    font-size: 20px;
                    font-weight: 500;
                    color: #242c44;
                    padding-bottom: 12px;
                    @include media-breakpoint-down(lg){
                        font-size: 14px;
                    }
                }
                &-flex{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-bottom: 10px;
                    @include media-breakpoint-down(lg){
                        flex: 1 1 auto;
                    }
                    &-opt{
                        font-size: 18px;
                        font-weight: normal;
                        color: #636363;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                    &-price{
                        font-size: 18px;
                        font-weight: 500;
                        color: #242c44;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                }
            }
            &-sub{
                border-top: solid 1px #e2e2e2;
                padding-top: 17px;
                &-flex{
                    margin-bottom: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    &-label{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                    }
                    &-price{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        @include media-breakpoint-down(lg){
                            font-size: 14px;
                        }
                        &.promo{
                            color: #c00;
                        }
                    }
                }
                &-from{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 0 10px;
                    margin-bottom: 20px;
                    .form-control{
                        height: 49px;
                    }
                    .btn-base{
                        max-width: 160px;
                        height: 49px;
                    }
                }
            }
        }
        &-price{
            padding: 20px 20px 21px 19px;
            border: solid 1px #e2e2e2;
            background-color: #fff;
            margin-bottom: 30px;
            @include media-breakpoint-down(lg){
                display: none;
            }
            &-title{
                font-size: 20px;
                font-weight: 600;
                color: #1e1e1e;
                padding-bottom: 16px;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                }
            }
            &-sub{
                border-top: solid 1px #e2e2e2;
                padding-top: 17px;
                &-flex{
                    margin-bottom: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    &-label{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                    }
                    &-price{
                        font-size: 18px;
                        font-weight: 500;
                        color: #1e1e1e;
                        &.promo{
                            color: #c00;
                        }
                    }
                }
                &-from{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 0 10px;
                    margin-bottom: 20px;
                    .form-control{
                        height: 49px;
                    }
                    .btn-base{
                        max-width: 160px;
                        height: 49px;
                    }
                }
            }
        }
        &-method{
            &-label{
                font-size: 18px;
                font-weight: 600;
                color: #1e1e1e;
            }
        }
    }
}
.payment-modal{
    margin-top: 15px;
    &-title{
        font-size: 20px;
        font-weight: 600;
        color: #212121;
        margin-bottom: 15px;
    }
    &-flex{
        margin-bottom: 15px;
        display: flex;
        gap: 0 20px;
        &-label{
            font-size: 20px;
            font-weight: normal;
            color: #212121;
            max-width: 200px;
        }
        &-opt{
            font-size: 20px;
            font-weight: 500;
            color: #212121;
        }
        &-between{
            margin-bottom: 44px;
            margin-top: 35px;
            display: flex;
            gap: 0 20px;
            justify-content: space-between;
            &-label{
                font-size: 20px;
                font-weight: 600;
                color: #212121;
                max-width: 200px;
            }
        }
    }
}
.failed-modal{
    text-align: center;
    &-title{
        font-size: 30px;
        font-weight: 600;
        color: #c00;
        margin-bottom: 34px;
        margin-top: 43px;
        @include media-breakpoint-down(lg){
            font-size: 20px;
        }
    }
    &-text{
        font-size: 14px;
        font-weight: normal;
        line-height: 1.64;
        color: #212121;
        margin-bottom: 3.8rem;
    }
    .btn-outline{
        @include media-breakpoint-down(lg){
            max-width: 200px;
            margin: 0 auto;
            border: 0;
        }
    }
    .btn-base{
        @include media-breakpoint-down(lg){
            max-width: 200px;
            margin: 0 auto;
        }
    }
}