/* Reset css for common drowser style reset*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
  font-family: 'Montserrat', sans-serif;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
img{
  max-width: 100%;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
p{
  line-height: 1.5;
}
a{
  text-decoration: none;
  color: var(--big-heading-color);
  transition: all 0.5s ease-in-out;
  &:hover{
    color: var(--base-theme-blue);
  }
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

//maincontainer
.container{
  max-width: 1170px;
}

.page-top{
  padding-top: 10rem;
  @include media-breakpoint-down(lg){
    
  }
}

/* Common background color */
.green-bg{
  background-color: var(--green-bg-color) !important;
}

/* Text Input design style */
.field-group{
  .errorText{
    font-size: 12px;
    font-weight: 500;
    color: #c00;
    text-align: left;
    display: none;
    margin-top: 5px;
    &.show{
      display: flex;
    }
  }
  .form-control{
    height: 50px;
    border: solid 1px #a8a8a8;
    background-color: #fff;
    &.error{
      border: solid 1px var(--red-text-color);
    }
    &:active, &:focus{
      box-shadow: none;
      outline: none;
      border: solid 1px #a8a8a8;
      background-color: #fff;
    }
  }
  //Text area design style
  textarea.form-control{
    min-height: auto;
    height: auto;
    border: solid 1px #a8a8a8;
    &.error{
      border: solid 1px var(--red-text-color);
    }
    &:active, &:focus{
      box-shadow: none;
      outline: none;
      border: solid 1px #a8a8a8;
      background-color: #fff;
    }
  }
}
// Select field design style
.form-select{
  height: 50px;
  border: solid 1px #a8a8a8;
  background-color: #fff;
  &.error{
    border: solid 1px var(--red-text-color);
  }
  &:active, &:focus{
    box-shadow: none;
    outline: none;
    border: solid 1px #a8a8a8;
    background-color: #fff;
  }
}

/* This price class need to use as wrap for price field. */
.price-field{
  position: relative;
  &:after{
    content: '$';
    font-size: 18px;
    font-weight: 600;
    color: #242c44;
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
}
.percent-field{
  position: relative;
  &:after{
    content: '%';
    font-size: 18px;
    font-weight: 600;
    color: #242c44;
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
} 
.attach-photo{
  position: relative;
  text-indent: -118px;
  line-height: 32px;
  &:after{
    content: '';
    width: 26px;
    height: 26px;
    background: url(../../images/icons/camera-big.svg) no-repeat;
    background-size: 100%;
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
  }
}

//Browse image field design style
.uploadImage{
  border-radius: 2px;
  border: dashed  1px #a8a8a8;
  background-color: #fff;
  display: flex;
  align-items: center;
  padding: 2.7rem 2rem 3rem;
  label{
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    gap: 14px 0;
    flex: 1 1 100%;
    max-width: 100%;
    &:before{
      content: '';
      width: 68px;
      height: 58px;
      background: url(../../images/icons/browse-image.svg) no-repeat;
      margin-bottom: 10px;
    }
  }
  .form-control{
    display: none;
  }
}

//Image design style
.user-image{
  max-width: 54px;
  height: 54px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
  background: url(../../images/feed/user-placeholder.png) no-repeat;
  background-size: 100%;
  flex: 1 1 100%;
  img{
      width: 100%;
      height: 100%;
      object-fit: cover;
  }
}

// Password field design style
.password{
  position: relative;
  &-view{
    cursor: pointer;
    &:after{
      content: "";
      background: url(../../images/icons/view--off--filled.svg) no-repeat;
      width: 24px;
      height: 24px;
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }                                                                                                                                                   
    &.show{
      &:after{
        content: "";
        background: url(../../images/icons/view--filled.svg) no-repeat;
        width: 24px;
        height: 24px;
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
}
/* Text Input label design style */
.form-label{
  font-size: 18px;
  font-weight: 500;
  color: #636363;
  margin-bottom: 12px;
}

.cancel-text{
  font-size: 18px;
  font-weight: 600;
  color: #283663;
  cursor: pointer;
  padding-left: 20px;
  padding-right: 20px;
  &:hover{
    color: var(--primary-small-text);
  }
}

/* button base design style */
.btn-base{
  height: 50px;
  padding: 16px 15px;
  background-color: var(--btn-base-bg);
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  color: #fff;
  letter-spacing: 0.76px;
  text-transform: uppercase;
  width: 100%;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0;
  &:hover{
    background-color: var(--primary-small-text);
  }
  &.disabled, &:disabled{
    background-color: var(--btn-base-bg);
    opacity: 1;
  }
  &:focus, &:active{
    box-shadow: none;
    background-color: var(--btn-base-bg);
    outline: none;
  }
  @include media-breakpoint-down(lg){
    font-size: 13px;
  }
}
.btn-outline{
  border: solid 1px var(--btn-base-bg);
  color: var(--btn-base-bg);
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  padding: 10px 15px;
  height: 50px;
  background: transparent;
  letter-spacing: 0.76px;
  text-transform: uppercase;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0;
  &:hover, &:focus, &:active{
    background-color: var(--primary-small-text);
  }
  &.withIcon{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0 15px;
    transition: all 0.5s ease-in-out;
    &:after{
      content: '';
      width: 15px;
      height: 6px;
      background: url(../../images/icons/button-arrow.svg) no-repeat;
    }
    &:hover, &:focus, &:active{
      justify-content: space-around;
      &:after{
        content: '';
        filter: brightness(0) invert(1);
      }
    }
  }
}
.btn, .btn-close{
  &:focus, &:active{
    border: none !important;
    box-shadow: none !important;
    outline: none !important;
  }
}
.btn-text{
  font-size: 14px;
  font-weight: bold;
  color: var(--btn-base-bg);
  border: 0;
  background: transparent;
  text-transform: uppercase;
  &:hover{
    background: transparent;
    color: var(--btn-base-bg);
  }
  &:hover, &:focus, &:active{
    background-color: transparent;
    border: none;
    box-shadow: none;
    outline: none;
    color: var(--btn-base-bg);
  }
  &.disabled{
    background-color: transparent;
    border: none;
    box-shadow: none;
    outline: none;
    color: #cacbce;
    cursor: no-drop;
  }
}

/* Social button design style */
.like-btn{
  display: flex;
  align-items: center;
  gap: 0 7px;
  background: none !important;
  border: 0;
  justify-content: flex-start;
  color: #212121 !important;
  font-size: 14px;
  font-weight: 500;
  position: relative;
  padding: 0 !important;
  &:before{
    content: '';
    width: 24px;
    height: 24px;
    background: url(../../images/social-icon/thumbs-up.svg) no-repeat;
  }
  &:hover, &:focus{
    background: none;
    border: 0 !important;
    color: #212121;
    box-shadow: none !important;
    outline: 0 !important;
  }
  &.liked, &:active{
    background: none;
    border: 0 !important;
    color: #212121;
    box-shadow: none !important;
    outline: 0 !important;
    &:before{
      content: '';
      width: 24px;
      height: 24px;
      background: url(../../images/social-icon/thumbs-up-liked.svg) no-repeat;
    }
  }
}
.comment-btn{
  display: flex;
  align-items: center;
  gap: 0 7px;
  background: none !important;
  border: 0;
  justify-content: flex-start;
  color: #212121 !important;
  font-size: 14px;
  font-weight: 500;
  position: relative;
  padding: 0 !important;
  &:before{
    content: '';
    width: 22px;
    height: 22px;
    background: url(../../images/social-icon/add-comment.svg) no-repeat;
  }
  &:hover, &:focus, &:active{
    background: none;
    border: 0 !important;
    color: #212121;
    box-shadow: none !important;
    outline: 0 !important;
  }
}
.button-text{
  background: none !important;
  border: 0;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.67;
  color: var(--primary-color);
  padding: 0;
  &:hover, &:focus, &:active{
    background: none;
    border: 0 !important;
    color: #212121;
    box-shadow: none !important;
    outline: 0 !important;
  }
}

/* Common page title design style */
.pageTitle{
  font-size: 38px;
  font-weight: 800;
  letter-spacing: normal;
  color: var(--primary-small-text);
  @include media-breakpoint-down(lg){
    font-size: 25px;
    font-weight: 600;
  }
}
.main-top-padding{
  padding-top: 7.6rem;
  @include media-breakpoint-down(lg){
    padding-top: 8rem;
  }
}

/* Tabl panel design style */
.tabPanel{
  border: 0;
  padding-bottom: 3.5rem;
  display: flex;
  justify-content: center;
  @include media-breakpoint-down(lg){
    padding-bottom: 1.5rem;
  }
  .nav-link{
    font-size: 25px;
    font-weight: 600;
    color: #a8a8a8;
    border: 0;
    &.active{
      color: var(--btn-base-bg);
    }
  }
}

/* for checkbox design style */
.form-check{
  &-input{
    border: solid 1px #636363;
    width: 13px;
    height: 13px;
    border-radius: 2px !important;
  }
  &-label{
    color: #1e1e1e;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
  }
}

/* Listing type UI LI design style */
ul.list-group{
  li, &-item{
    border: 0;
    border-radius: 0;
    font-size: 14px;
    font-weight: 500;
    color: var(--primary-small-text);
    position: relative;
    padding: 8px 15px;
    &:before{
      content: '';
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: var(--primary-small-text);
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

//Category page title design style
.title-with-dash{
  font-size: 30px;
  font-weight: 600;
  color: #242c44;
  margin-bottom: 2rem;
  position: relative;
  text-align: center;
  &:after{
    content: '';
    width: 60px;
    height: 3px;
    background-color: #283663;
    position: absolute;
    bottom: -18px;
    left: 50%;
    transform: translateX(-50%);
  }
}
/* Modal design style */
.modal-backdrop{
  opacity: 0.4;
  background-color: #5b5b5b;
}
body.modal-open .App{
  -webkit-filter: blur(4px);
  -moz-filter: blur(4px);
  -o-filter: blur(4px);
  -ms-filter: blur(4px);
  filter: blur(4px);
  filter:progid:DXImageTransform.Microsoft.Blur(PixelRadius='4');
}
.custom-modal{
  &.modal{
      .modal-dialog{
          max-width: 640px;
          box-shadow: 0 3px 17px 0 rgba(0, 0, 0, 0.05);
          background-color: #fff;
          padding: 45px 40px;
          margin-top: 8rem;
          @include media-breakpoint-down(lg){
              padding: 20px 0px;
              margin-top: 6rem;
          }
          position: relative;
          .modal-content{
              border: 0;
              position: initial;
          }
      }
      .modal-header{
          border: 0;
          display: block;
          .btn-close{
              position: absolute;
              right: 20px;
              top: 20px;
              background: url(../../images/icons/close.svg) no-repeat;
              opacity: 1;
          }
      }
      .modal-title{
          font-size: 38px;
          font-weight: bold;
          color: #242c44;
          display: block;
          text-align: center;
          @include media-breakpoint-down(lg){
              font-size: 22px;
          }
      }
      .modal-footer{
          border: 0;
          padding-bottom: 0;
      }
  }
  .modal-card{
    &-main{
      border: 0;
      .User-title{
        font-size: 18px;
        font-weight: 600;
        color: #283663;
      }
      &-image{
        padding: 10px;
        border-radius: 10px;
        border: solid 1px #e2e2e2;
        background-color: #fff;
        margin-bottom: 20px;
      }
      .card-body{
        padding: 0;
        .card-title{
          font-size: 20px;
          font-weight: 600;
          color: #242c44;
          margin-bottom: 10px;
        }
        .card-text{
          font-size: 14px;
          font-weight: normal;
          color: #1e1e1e;
        }
      }
    }
  }
  
  
  .bid-modal{
      &-details{
          margin: 35px 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          @include media-breakpoint-down(lg){
              margin: 5px 0 25px;
          }
          &-label{
              font-size: 18px;
              font-weight: 600;
              color: #212121;
          }
          &-price{
              font-size: 18px;
              font-weight: 600;
              color: #242c44;
          }
      }
  }
}

/* Mobile column reset padding style */
.reset-padding-mobile{
  @include media-breakpoint-down(sm){
    padding-left: 0;
    padding-right: 0;
  }
}

/* checkbox switch design guide */
.p-auction-label{
  display: flex;
  gap: 0 20px;
  align-items: center;
  &-title{
    font-size: 25px;
    font-weight: 600;
    color: #242c44;
    @include media-breakpoint-down(lg){
      font-size: 18px;
    } 
  }
  &-checkbox{
    .form-check{
      background-color: #979797;
      width: 46px;
      height: 27px;
      border-radius: 13.5px;
      overflow: hidden;
      &-input{
        background-color: #979797;
        width: 46px;
        height: 27px;
        border: 0;
        margin-top: 0;
        background: url(../../images/icons/toggle.svg) no-repeat;
        transition: all 0.3s ease-in;
        cursor: pointer;
        &:checked{
          background: url(../../images/icons/toggle-active.svg) no-repeat;
        }
        &:hover, &:focus, &:active{
          border: 0 !important;
          box-shadow: none !important;
          outline: 0 !important;
        }
      }
    }
  }
}

/* Dropdown design style */
.custom-dropdown{
  &-menu{
    padding: 15px 25px 11px 25px;
    border-radius: 5px;
    box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.06);
    border: solid 1px #f3f3f3;
    background-color: #fbfbfb;
    &-row{
      display: flex;
      gap: 0 8px;
      align-items: center;
      justify-content: flex-start;
      &-icon{}
      &-title{
        font-size: 14px;
        font-weight: 500;
        color: #242c44;
      }
    }
  }
  &.show{
    .dropdown-toggle{
      border: 0;
      color: transparent;
      background-color: #eee;
      &:hover{
        background-color: #eee;
        color: transparent;
      }
      &:after{
        border: 0;
      }
    }
  }
  .dropdown-toggle{
    border: 0;
    color: transparent;
    &:hover{
      background-color: #eee;
      color: transparent;
    }
    &:after{
      border: 0;
    }
  }
  .dropdown-item{
    padding: 10px 0;
    .custom-dropdown-menu-row-icon{
      filter: invert(14%) sepia(19%) saturate(1380%) hue-rotate(187deg) brightness(94%) contrast(90%);
    }
    &:hover{
      background: none;
      .custom-dropdown-menu-row-title{
        color: #ee0707;
      }
      .custom-dropdown-menu-row-icon{
        filter: invert(36%) sepia(63%) saturate(7448%) hue-rotate(354deg) brightness(82%) contrast(136%);
      }
    }
  }
  .seller-status-overflowBtn{
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 2px;
      background-color: rgba(238,238,238, .5);
      border-radius: 5px;
      cursor: pointer;
      &:after{
          content: "";
          width: 30px;
          height: 30px;
          background: url(../../images/icons/overflow-menu--horizontal.svg) no-repeat;
      }
  }
}

// Profile image round
.profile-image-round{
  width: 170px;
  height: 170px;
  border-radius: 50px;
  position: relative;
  margin: 0px auto 40px;
  @include media-breakpoint-down(lg){
    width: 140px;
    height: 140px;
  } 
  .edit-profile-icon{
    width: 76px;
    height: 76px;
    border-radius: 50%;
    box-shadow: 0 4px 52px 0 rgba(0, 0, 0, 0.16);
    background-color: #fff;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    @include media-breakpoint-down(lg){
      width: 42px;
      height: 42px;
    } 
    &:after{
      content: '';
      width: 46px;
      height: 46px;
      background: url(../../images/icons/camera-on-photo.svg) no-repeat;
      background-size: 100%;
      @include media-breakpoint-down(lg){
        width: 26px;
        height: 26px;
      } 
    }
  }
}