@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.s-category{
    .searchLabelmobile{
        display: none;
        @include media-breakpoint-down(lg){
            display: block;
            font-size: 18px;
            font-weight: 500;
        }
    }
    .showingText{
        &-search{
            margin-bottom: 20px;
            display: block;
        @include media-breakpoint-down(lg){
            display: block;
            position: absolute;
            right: 15px;
        }
        .searchLabeldesktop{
            @include media-breakpoint-down(lg){
                display: none;
            }
        }
        }
        .searchLabeldesktop{
            @include media-breakpoint-down(lg){
                display: none;
            }
        }
    }
    .searchMobile{
        margin: 15px 0;
        display: none;
        @include media-breakpoint-down(lg){
            display: flex;
        }
        .searchWrap{
            width: 100%;
        }
        .s-input{
            display: flex;
        }
        .searchIcon{
            display: none;
        }
    }
}