@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.noCart{
    display: flex;
    flex-direction: column;
    align-items: center;
    &-img{
        padding-bottom: 2.3rem;
    }
    &-title{
        font-size: 20px;
        font-weight: 600;
        color: #242c44;
        padding-bottom: 10px;
        text-align: center;
    }
    &-text{
        font-size: 18px;
        font-weight: normal;
        color: #212121;
        padding-bottom: 3rem;
        text-align: center;
    }
    .btn-outline{
        max-width: 196px;
    }
}