@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.newsFeed{
    .n-feed{
        padding: 20px;
        margin-bottom: 15px;
        @include media-breakpoint-down(sm){
            border-left: 0;
            border-right: 0;
            border-bottom: 0;
        } 
        .user-image{
            max-width: 54px;
            max-height: 54px;
            overflow: hidden;
            border-radius: 50%;
            margin-right: 10px;
            background: url(../../assets/images/feed/user-placeholder.png) no-repeat;
            background-size: 100%;
            @include media-breakpoint-down(lg){
                max-width: 34px;
                max-height: 34px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .User-title{
            h6{
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 4px;
                @include media-breakpoint-down(lg){
                    font-size: 14px;
                    margin-bottom: 2px;
                }   
            }
            span{
                font-size: 14px;
                font-weight: 500;
                color: #636363;
            }
        }
        &-image{
            text-align: center;
            img{
                max-width: 361px;
                margin: 0 auto;
            }
        }
        &-body{
            border-bottom: solid 1px #e2e2e2;
            padding-left: 0 !important;
            padding-right: 0 !important;
            padding-bottom: 24px !important;
            margin-bottom: 10px;
            .card-title{
                font-size: 20px;
                font-weight: 600;
                line-height: normal;
                margin-bottom: 20px;
                @include media-breakpoint-down(lg){
                    font-size: 18px;
                }
            }
            .card-text{
                font-size: 14px;
                font-weight: 400;
                line-height: 1.43;
            }
        }
    }
    .l-comment{
        &-meta{
            padding-bottom: 15px;
            border-bottom: solid 1px #e2e2e2;
            margin-bottom: 20px;
            &-count{
                font-size: 14px;
                font-weight: 500;
                color: #212121;
                margin-bottom: 18px;
                display: flex;
            }
            &-button{
                display: flex;
                align-items: center;
                gap: 0 60px;
            }
        }
        &-box{
            &-post{
                margin-bottom: 15px;
                .user-image{
                    max-width: 40px;
                    max-height: 40px;
                    overflow: hidden;
                    border-radius: 50%;
                    margin-right: 10px;
                    @include media-breakpoint-down(lg){
                        max-width: 38px;
                        max-height: 38px;
                    }
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .User-title{
                    h6{
                        font-size: 14px;
                        font-weight: 600;
                        margin-bottom: 4px;
                    }
                    p{
                        font-size: 12px;
                        font-weight: normal;
                        color: #212121;
                        line-height: 1.67;
                    }
                }
            }
            &-form{
                flex-wrap: wrap;
                .user-image{
                    max-width: 40px;
                    max-height: 40px;
                    overflow: hidden;
                    border-radius: 50%;
                    margin-right: 10px;
                    @include media-breakpoint-down(lg){
                        max-width: 38px;
                        max-height: 38px;
                    }
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .User-form{
                    flex: 1 1 auto;
                    max-width: 100%;
                    position: relative;
                    .form-control{
                        border-radius: 5px;
                        height: 40px;
                        font-size: 12px;
                        color: #979797;
                        padding-right: 57px;
                    }
                }
                &-postBtn{
                    cursor: pointer;
                    font-weight: 500;
                    position: absolute;
                    top: 50%;
                    right: 10px;
                    transform: translateY(-50%);
                    font-size: 14px;
                    /* background: #fff; */
                    height: 100%;
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
}
