@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins/_breakpoints';
.footer{
    border-top: solid 1px #ececec;
    padding: 40px 20px;
    @media screen and (min-width: 1600px) {
        padding: 40px 90px;
      }
    @include media-breakpoint-down(lg){
        padding: 20px 15px;
        flex-direction: column;
        gap: 20px 0;
        margin-top: 2rem;
    }
    &-nav{
        ul{
            display: flex;
            flex-flow: wrap;
            align-items: center;
            gap: 0 35px;
            @include media-breakpoint-down(lg){
                justify-content: center;
                margin-bottom: 25px;
            }
            li{
                a{
                    font-size: 14px;
                    font-weight: 500;
                    color: #242c44;
                    text-decoration: none;
                    &:hover{
                        color: #283663;
                    }
                }
            }
        }
    }
    .s-icon{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 0 12px;
        @include media-breakpoint-down(lg){
            justify-content: center;
            margin-top: 20px;
        }
        li{
            width: 40px;
            height: 40px;
            display: flex;
            @include media-breakpoint-down(md){
                width: 20px;
                height: 20px;
            }
            a{
                display: flex;
            }
        }
    }
}
